import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { NavLink } from "react-router-dom";

import shareService from "../../../services/share.service";
import QuantityEditor from "../service/quantityEditor.service.js";
import "./CheckOut.scss";

const config = {
  steps: [
    {
      number: 1,
      label: "Billing & Shipping"
    },
    {
      number: 2,
      label: "Your order & Payment"
    },
    {
      number: 3,
      label: "Confirmation"
    }
  ]
};

function CheckOut({ cart, setCart, removeProductFromCart }) {
  const [activeStep, setActiveStep] = useState(1);
  const [quantityEditor, setQuantityEditor] = useState([]);
  const [isInit, setIsInit] = useState(false);

  const setOrderInfo = () => {
    const newCart = {
      ...cart,
      products: quantityEditor.map(item => item.orderInfo)
    };

    quantityEditor.length && setCart(newCart);
  };

  useEffect(() => {
    if (!quantityEditor.length && cart.products.length) {
      var initQuantityEditor = Array(cart.products.length).fill(
        new QuantityEditor({ isAllow0: false })
      );

      cart.products.forEach((productInfo, i) => {
        initQuantityEditor[i].orderInfo = shareService.deepClone(productInfo);
        initQuantityEditor[i].setOrderInfo = setOrderInfo;
      });

      setQuantityEditor(initQuantityEditor);
    }
  }, [cart]);

  useEffect(() => {
    if (quantityEditor.length && !isInit) {
      var initQuantityEditor = [...quantityEditor];

      initQuantityEditor.forEach((item, i) => {
        initQuantityEditor[i].setOrderInfo = setOrderInfo;
      });

      setQuantityEditor(initQuantityEditor);
      setIsInit(true);
    }
  });

  const changeStep = (number) => {
    setActiveStep(number);
  };

  return (
    <section className='tm-checkout'>
      <ul className='tm-checkout__steps-container'>
        {config.steps.map(step => (
          <li
            key={step.number}
            className={
              "tm-checkout__step " +
              (activeStep === step.number && "tm-checkout__step--active")
            }
            onClick={changeStep.bind(this, step.number)}
          >
            <span className='tm-checkout__step-number'>{step.number}</span>
            <a href='#' className='tm-checkout__step-label'>
              {step.label}
            </a>
          </li>
        ))}
      </ul>
      <div className='tm-checkout__content'>
          <h2>ĐƠN HÀNG ĐÃ ĐẶT THÀNH CÔNG</h2>
          <p className='woocommerce-thankyou-order-received'>
            Cảm ơn bạn. Chúng tôi đã nhận được đơn hàng của bạn và sẽ liên lạc
            lại trong vòng 4-5h tới. Chúng tôi ship COD cho bạn (các bạn nhận
            hàng rồi mới thanh toán) và chúng tôi sẽ dùng dịch vụ ship hàng
            nhanh nhất và rẻ nhất cho các bạn Cảm ơn các bạn đã mua hàng.
          </p>
          <ul className='tm-checkout__order-summary'>
            <li className='order'>
              Order Number: <strong>11800</strong>
            </li>
            <li className='date'>
              Date: <strong>16/05/2019</strong>
            </li>
            <li className='total'>
              Total:{" "}
              <strong>
                <span className='woocommerce-Price-amount amount'>
                  2,230,000&nbsp;
                  <span className='woocommerce-Price-currencySymbol'>VNĐ</span>
                </span>
              </strong>
            </li>
            <li className='method'>
              Payment Method: <strong>Trả tiền mặt khi nhận hàng (COD)</strong>
            </li>
          </ul>
          <div className='clear' />
          <h2>Order Details</h2>
          <table className='tm-checkout__order-details'>
            <thead>
              <tr>
                <th className='product-name'>Sản phẩm</th>
                <th className='product-total'>Tổng cộng</th>
              </tr>
            </thead>
            <tbody>
              <tr className='tm-checkout__order-product'>
                <td className='woocommerce-table__product-name product-name'>
                  <a href='https://tummachines.com/shop/big-pocket-short-black/?attribute_pa_size=l'>
                    BIG POCKET SHORT - BLACK - L
                  </a>{" "}
                  <strong className='product-quantity'>× 3</strong>{" "}
                </td>
                <td className='woocommerce-table__product-total product-total'>
                  <span className='woocommerce-Price-amount amount'>
                    1,050,000&nbsp;
                    <span className='woocommerce-Price-currencySymbol'>
                      VNĐ
                    </span>
                  </span>{" "}
                </td>
              </tr>
              <tr className='tm-checkout__order-product'>
                <td className='woocommerce-table__product-name product-name'>
                  <a href='https://tummachines.com/shop/war-inside-my-head-tshirt-white/?attribute_pa_size=l'>
                    / WAR INSIDE MY HEAD / TSHIRT - WHITE - L
                  </a>{" "}
                  <strong className='product-quantity'>× 1</strong>{" "}
                </td>
                <td className='woocommerce-table__product-total product-total'>
                  <span className='woocommerce-Price-amount amount'>
                    350,000&nbsp;
                    <span className='woocommerce-Price-currencySymbol'>
                      VNĐ
                    </span>
                  </span>{" "}
                </td>
              </tr>
              <tr className='tm-checkout__order-product'>
                <td className='woocommerce-table__product-name product-name'>
                  <a href='https://tummachines.com/shop/war-inside-my-head-tshirt-black/?attribute_pa_size=l'>
                    / WAR INSIDE MY HEAD / TSHIRT - BLACK - L
                  </a>{" "}
                  <strong className='product-quantity'>× 1</strong>{" "}
                </td>
                <td className='woocommerce-table__product-total product-total'>
                  <span className='woocommerce-Price-amount amount'>
                    350,000&nbsp;
                    <span className='woocommerce-Price-currencySymbol'>
                      VNĐ
                    </span>
                  </span>{" "}
                </td>
              </tr>
              <tr className='tm-checkout__order-product'>
                <td className='woocommerce-table__product-name product-name'>
                  <a href='https://tummachines.com/shop/essential-denim-mid-blue/?attribute_pa_size=28'>
                    ESSENTIAL DENIM - MID BLUE - 28
                  </a>{" "}
                  <strong className='product-quantity'>× 1</strong>{" "}
                </td>
                <td className='woocommerce-table__product-total product-total'>
                  <span className='woocommerce-Price-amount amount'>
                    480,000&nbsp;
                    <span className='woocommerce-Price-currencySymbol'>
                      VNĐ
                    </span>
                  </span>{" "}
                </td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <th scope='row'>Tổng số phụ:</th>
                <td>
                  <span className='woocommerce-Price-amount amount'>
                    2,230,000&nbsp;
                    <span className='woocommerce-Price-currencySymbol'>
                      VNĐ
                    </span>
                  </span>
                </td>
              </tr>
              <tr>
                <th scope='row'>Giao nhận hàng:</th>
                <td>COD</td>
              </tr>
              <tr>
                <th scope='row'>Payment method:</th>
                <td>Trả tiền mặt khi nhận hàng (COD)</td>
              </tr>
              <tr>
                <th scope='row'>Tổng cộng:</th>
                <td>
                  <span className='woocommerce-Price-amount amount'>
                    2,230,000&nbsp;
                    <span className='woocommerce-Price-currencySymbol'>
                      VNĐ
                    </span>
                  </span>
                </td>
              </tr>
            </tfoot>
          </table>
      </div>
    </section>
  );
}

CheckOut.propTypes = {
  cart: PropTypes.object.isRequired,
  setCart: PropTypes.func.isRequired,
  removeProductFromCart: PropTypes.func.isRequired
};

export default CheckOut;
